/* eslint-disable */

import Web3 from "web3";
import contract from "truffle-contract";
import ua from "../build/contracts/VIDToken.json";
import pa from "../build/contracts/Gong.json";
import uh from "../build/contracts/uHive.json";
import topa from "../build/contracts/Top.json";

export default {

  async initWeb3Account(callback) {
    if (window.ethereum) {
      this.provider = window.ethereum;
      try {
        await window.ethereum.enable();
      } catch (error) {
        console.error("User denied account access");
      }
    } else if (window.web3) {
      this.provider = window.web3.currentProvider;
    } else {
      this.provider = new Web3.providers.HttpProvider("http://127.0.0.1:7545");
    }
    this.web3 = new Web3(this.provider);

    web3.eth.getAccounts((err, accs) => {
      if (err != null) {
        console.error("无法获取账号， 是否安装了 Metamask");
        this.message = "";
        return;
      }

      if (accs.length === 0) {
        console.error("无法获取账号，Metamask 时候正确配置.");
        return;
      }
      // this.account = ;
      callback(this.web3, accs[0])
    })
  },

  getVidToken() {
    const Ucc = contract(ua)
    Ucc.setProvider(this.provider)
    return Ucc.deployed();
  },

  getPagoda() {
    const Pagoda = contract(pa)
    Pagoda.setProvider(this.provider)
    return Pagoda.deployed();
  },

  getuHive() {
    const uHive = contract(uh)
    uHive.setProvider(this.provider)
    return uHive.deployed()
  },

  getTop() {
    const Top = contract(topa)
    Top.setProvider(this.provider)
    return Top.deployed()
  },

  getRound(initTime) {
    const timestamp = new Date().getTime() / 1000;
    return Math.floor((timestamp - initTime) / 1209600);
  },

  getLeftTime(initTime) {
    const timestamp = new Date().getTime() / 1000;
    return 7 - Math.floor(((timestamp -initTime) % 1209600) / 86400);
  }
}

