import Vue from 'vue'
import Router from 'vue-router'
import Vid from './components/Vid.vue'
import Admin from './components/Admin.vue'
import Init from './components/Init.vue'
import uHive from './components/uHive.vue'
import Ranking from './components/Ranking.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/p',
      name: 'vid',
      component: Vid
    },
    {
      path: '/p/:p',
      name: 'vid',
      component: Vid
    },
    {
      path: '/uhive',
      name: 'uhive',
      component: uHive
    },
    {
      path: '/admin',
      name: 'admin',
      component: Admin
    },
    {
      path: '/init',
      name: 'init',
      component: Init
    },
     {
      path: '/ranking',
      name: 'ranking',
      component: Ranking
    }
  ]
  }
)

