<template>
<div class="">

  <el-card shadow="never">
    <p>当前网络：{{ networkName }} , 账号: {{ account }}</p>
    <p>当前账号VID数量： {{ accountuccbalance }} VID </p>
    <p>共振塔VID数量： {{ guccbalance }} VID </p>
    <p>uHive VID数量： {{ uuccbalance }} VID </p>

      <el-button @click="uccToPagoda">转移 VID 到共振塔 </el-button>
      <el-button @click="uccToHive">转移 VID 到uHive</el-button>


    <el-input placeholder="请输新管理员地址" v-model="newOwner"></el-input>
    <el-button @click="setMgr"> 修改管理员 </el-button>

  </el-card>

  <div>
    顶点推广链接: <el-link type="primary" :href="uLink"> {{ uLink }} </el-link>
  </div>

  <p>
    Token 合约: {{tokenAddr}}
  </p>


</div>
</template>

<script>
/* eslint-disable */
import MyVid from "./../my_vid.js"

export default {
  name: 'Init',
  data() {
    return {
      uLink: null,
      account: null,
      uuccbalance: null,
      guccbalance: null,
      networkName: null,
      accountuccbalance: null,
      lotteryAddr: null,
      luckyAddr: null,
      tokenAddr: null,
      newOwner: null,
      attachLuckyAddr: null,
      attachLotteyAddr: null
    }
  },

  created() {

    MyVid.initWeb3Account((web3, acc) => {
      this.account = acc
      this.web3 = web3
      this.getNetWork()
      this.init();
    });
  },
  methods: {

    async init() {
      this.pagoda = await MyVid.getPagoda()
      this.token = await MyVid.getVidToken()
      this.uhive = await MyVid.getuHive()
      this.top = await MyVid.getTop()

      this.info()

    },

    getNetWork() {
      var network = this.web3.eth.net.getId((err, network) => {
        console.log(network)

        if (network == 1) {
          this.networkName = "主网"
        } else if (network == 3) {
          this.networkName = "Ropsten测试网"
        } else if (network == 4) {
          this.networkName = "Rinkeby测试网"
        } else if (network == 42) {
          this.networkName = "Kovan测试网"
        } else {
          this.networkName = "Ganache"
        }
      })
    },

    info() {
      this.tokenAddr = this.token.address

      this.uLink = process.env.VUE_APP_HOST + 'p/' + this.top.address
      this.token.balanceOf(this.account, {
        from: this.account
      }).then(r => {
        this.accountuccbalance = (r / 10000)
        console.log('ucc' + this.accountuccbalance)
      })

      this.token.balanceOf(this.pagoda.address, {
        from: this.account
      }).then(r => {
        this.guccbalance = (r / 10000)
        console.log('ucc' + this.uccbalance)
      })

      this.token.balanceOf(this.uhive.address, {
        from: this.account
      }).then(r => {
        this.uuccbalance = (r / 10000)
        console.log('ucc' + this.uuccbalance)
      })
    },

    setMgr() {
      this.top.setMgr(this.newOwner, {
        from: this.account
      })
    },

    uccToPagoda() {
      this.token.transfer(this.pagoda.address, this.web3.utils.toHex(10000000000000), {
        from: this.account
      }).then(r => {
        this.info()
      })
    },

    uccToHive() {
      this.token.transfer(this.uhive.address, this.web3.utils.toHex(14000000000000), {
        from: this.account
      }).then(r => {
        this.info()
      })
    }


  }
}
</script>

<style scoped>
.s-font {
  font-size: 10px;
}
</style>
