<template>
<div v-loading.fullscreen="loading" class="content content-on-tab">
  <div class="header">
    <div class="header-title"> VID Network </div>
  </div>

  <div class="title">
    <img src="../assets/wallet.svg" />
    <span>钱包余额</span>
  </div>
  <div class="balance-content">
    <div>
      <div class="icon-content">
        <span class="gold-font">VID</span>
      </div>
      <p class="gold-font">{{ fnd(vidblance, 0) }} </p>
    </div>
    <div>
      <div class="icon-content">
        <span class="gold-font"> ETH</span>
      </div>
      <p class="gold-font">{{ fn(ethblance) }} </p>
    </div>
  </div>

  <div class="title">
    <img src="../assets/b_icon.svg" />
    <span>共振/收益</span>
    <el-link  size="small" type="primary" href="http://localhost:8080/#/ranking" >排行榜> </el-link>
  </div>

  <div class="card-bkg mine-box">
    <div class="award-des">
      <span> 共振池 </span>
      <b class="gold-font"> 1ETH:{{ rate }}VID </b>
    </div>
    <el-button size="small" type="primary" @click="join">共振</el-button>
  </div>

  <div class="card-bkg mine-box ">
    <div class="award-des">
      <span> 推广奖金 </span>
      <b class="gold-font"> {{ pagodaAward }} ETH </b>
    </div>
    <el-button size="small" type="primary" @click="wdPagoda">提取</el-button>
  </div>

  <div class="title">
    <img src="../assets/pool.svg" />
    <span>矿池</span>
  </div>

  <div class="card-bkg mine-box ">
    <div class="award-des">
      <span> 我的算力: {{  }}  </span>
    </div>
    <el-link type="primary" href="http://localhost:8080/#/uhive" >前往矿池> </el-link>
  </div>

  <div v-if="isMember" class="card-bkg qrcode-box ">
    <p class="qrcode-title">我的推广二维码</p>
    <qrcode :value="uLink" :options="{margin: 1, width: 120 }"></qrcode>
  </div>


</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";
import MyVid from "./../my_vid.js";
import {
  MessageBox
} from 'element-ui';

export default {
  name: 'Vid',
  data() {
    return {
      web3: null,
      pagoda: null,
      lottery: null,
      lucky: null,
      round: 0,
      ethblance: 200,
      vidblance: 3000,
      rate: "~",
      pagodaAward: 0.0,
      lotteryAward: 0.0,
      lotteryAwardtips: "",
      islotteryWded: false,
      luckyAward: 0.0,
      account: null,
      isMember: false,
      uLink: null,
      loading: true,
      partners: 0
    }
  },

  created() {

    let addr = this.$route.params.p;

    if (addr && addr.length === 42) {
      this.parentAddr = addr;
    } else {
      console.log("no parent");
    }

    MyVid.initWeb3Account((web3, acc) => {
      console.log("account:" + acc)
      this.account = acc
      this.web3 = web3
      this.init()

    });
  },
  methods: {

    fn(num) {
      return formatNum(num, 2)
    },

    fnd(num, d) {
      return formatNum(num, d)
    },

    async init() {
      this.pagoda = await MyVid.getPagoda()
      this.token = await MyVid.getVidToken()

      this.info()
      this.awardInfo()
    },

    ethBalance() {
      this.web3.eth.getBalance(this.account, (err2, res2) => {
        if (!err2) {
          this.ethblance = this.web3.utils.fromWei(res2, 'ether')
        } else {}
      })
    },

    info() {
      this.ethBalance()

      this.token.balanceOf(this.account, {
        from: this.account
      }).then(r => {
        this.vidblance = (r / 10000)
      })

      this.pagoda.userEths(this.account, {
        from: this.account
      }).then(
        r => {
          this.loading = false
          if (this.web3.utils.fromWei(r, 'ether') >= 1) {
            this.isMember = true
            this.uLink = process.env.VUE_APP_HOST + 'p/' + this.account
          }
        }
      )
    },

    join() {

      if (!this.parentAddr) {

        MessageBox.alert("无效的邀请人! 请从你的邀请人获取链接进入共振。", "提示", {
          confirmButtonText: '确定'
        })

        return;
      } else {
        MessageBox.prompt("请输入共振的金额:", "", {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputType: "number",
          inputValue: 1,
          inputErrorMessage: "输入数量不正确"
        }).then(({value}) => {
          if (value < 1) {
            MessageBox.alert("输入数量不正确", "提示", {
              confirmButtonText: '确定'
            })
          } else {
            this.doJoin(value)
          }
        }).catch(() => {

        })

      }
    },

    doJoin(value) {
      var ethValue  = "" + value;
      console.log("doJoin:" + ethValue)

      this.pagoda.join(this.parentAddr, {
          from: this.account,
          value: this.web3.utils.toWei(ethValue, 'ether')
        }).then(r => {
          this.info()
        })
        // .catch(e => {
        //   console.error("共振失败" + e);
        // })
    },

    awardInfo() {
      this.pagoda.memberAward(this.account, {
        from: this.account
      }).then(
        r => {
          this.pagodaAward = this.fnd(this.web3.utils.fromWei(r, 'ether'), 4);
        }
      )

      this.pagoda.getRound(Math.floor(new Date().getTime() / 1000)).then(
        r => {
          console.log(" round: " + r.toNumber());
          this.round = r.toNumber()
          this.getRate(this.round)
        }
      )
    },

    getRate(round) {
      if (round == 0) {
        this.rate = 100000
      } else if (round == 1) {
        this.rate = 50000
      } else if (round == 2) {
        this.rate = 30000
      } else if (round == 3) {
        this.rate = 15000
      } else if (round == 4) {
        this.rate = 10000
      } else {
        // rate =  0;
      }
    },

    wdPagoda() {
      this.pagoda.withdraw({
        from: this.account
      }).then(r => {
        this.ethBalance()
      })
    }
  }
}
</script>

