<template>
<div v-loading.fullscreen="loading" class="content content-on-tab">

  <div class="header">
    <span class="el-icon-back back" @click="goBack"> </span>
    <div class="header-title">  VID 矿池 </div>
    <span class="back" @click="goBack"> </span>
  </div>

  <div class="title">
    <img src="../assets/pool.svg" />
    <span>矿池</span>
  </div>
  <div class="balance-content">
    <div>
      <div class="icon-content">
        <span class="gold-font">总算力</span>
      </div>
      <p class="gold-font">{{ totalPower }} </p>
    </div>
    <div>
      <div class="icon-content">
        <span class="gold-font">今日产量</span>
      </div>
      <p class="gold-font">{{ dayOutput }}W </p>
    </div>
  </div>

  <div class="card-bkg uhive-box">
    <span>我的算力：{{ fn(myStakes) }} T</span>
    <el-button size="small" type="primary" @click="wdStake">赎回</el-button>
  </div>

  <div class="card-bkg uhive-box">
    <span>伙伴算力：{{ fn( parterStakes) }} T</span>
  </div>

  <div class="card-bkg uhive-box">
    <span>已挖矿：{{ fn(myInterest) }} VID</span>
    <el-button size="small" type="primary" @click="wdInsterest">提取</el-button>
  </div>

  <p class="tips-font ">挖矿说明：<br />
    1. 通过抵押VID获得算力。 <br />
    2. 有下线参与挖矿时，将获得50%的伙伴算力。 <br />
    3. 按每日矿池常量算力比例分配给挖矿节点。 <br />
  </p>

  <div class="jbox">

    <el-button class = "mintbtn" size="medium" type="primary" @click="join">
      开始挖矿
    </el-button>

  </div>



</div>
</template>

<script>
/* eslint-disable */
import MyVid from "./../my_vid.js";
import formatNum from "./../numcomp";
import {
  MessageBox
} from 'element-ui';

export default {
  name: 'uHive',
  data() {
    return {
      web3: null,
      myStakes: "~",
      parterStakes: "~",
      myInterest: "~",
      totalPower: "~",
      dayOutput: "~",
      needRequestSuper: false,
      newStakeAmount: 1000,
      joinDialog: false,
      warningDialogVisible: false,
      loading: true
    }
  },

  created() {

    MyVid.initWeb3Account((web3, acc) => {
      console.log("account:" + acc)
      this.account = acc
      this.web3 = web3
      MyVid.getVidToken().then(ins => {
        this.token = ins
        return MyVid.getuHive()
      }).then(ins => {
        this.uHive = ins;
        this.powerInfo()
        this.interestInfo()
      })
    });
  },
  methods: {

    fn(num) {
      return formatNum(num, 2)
    },

    goBack() {
      this.$router.back(-1)
    },

    powerInfo() {
      const day = parseInt(new Date().getTime() / 1000 / 60 / 60)
      console.log("day:" + day)

     this.uHive.dayPowers(day, {
        from: this.account
      }).then(r => {
        console.log(r)
        this.totalPower = (r / 10000)
      })

      this.uHive.currDayOutput({
        from: this.account
      }).then(r => {
        this.dayOutput = (r / 100000000)
      })

      this.uHive.myPowers(this.account, {
        from: this.account
      }).then(r => {
        this.myStakes = (r / 10000)
      })

      this.uHive.appendedPowers(this.account, {
        from: this.account
      }).then(r => {
        this.parterStakes = (r / 10000)
      })

    },

    interestInfo() {
      this.uHive.myInterest({
        from: this.account
      }).then(
        r => {
          this.myInterest = r / 10000;
          this.loading = false
        }

      )
    },

    join() {
      MessageBox.prompt("抵押VID开始挖矿，抵押越多算力越高:", "", {
        confirmButtonText: '抵押',
        cancelButtonText: '取消',
        inputType: "number",
        inputValue: 100,
        inputErrorMessage: "输入数量不正确"
      }).then(({
        value
      }) => {
        if (value < 1) {
          MessageBox.alert("输入数量不正确", "提示", {
            confirmButtonText: '确定'
          })
        } else {
          this.doJoin(value)
        }
      }).catch(() => {})
    },

    doJoin(value) {
      let amount = value * 10000
      console.log(amount)
      this.token.transfer(this.uHive.address, this.web3.utils.toHex(amount), {
        from: this.account
      }).then(() => {
        this.interestInfo()
        this.powerInfo()
      })
    },

    wdStake() {
      console.log("wdStake")
      this.uHive.wdStake({
        from: this.account
      }).then(r => {
        this.powerInfo()
        this.interestInfo()
      })
    },

    wdInsterest() {
      console.log("wdInsterest")
      this.uHive.wdInsterest({
        from: this.account
      }).then(r => {
        this.interestInfo()
      })
    }

  }
}
</script>

