<template>
<div v-loading.fullscreen="loading">
  <div class="header">
    <span class="el-icon-back back" @click="goBack"> </span>
    <div class="header-title"> VID 排行榜 </div>
    <span class="back" @click="goBack"> </span>
  </div>

  <div class="content">

    <el-tabs :stretch=true type="border-card">
      <el-tab-pane label="推广大赛排名">

        <div class="award-tips">前10名分享奖金: <span class="gold-font balance-font">{{ lastAwardEth }}</span> ETH </div>

        <div class="addr-content">
          <div class="addr-font" v-bind:key="index" v-for="(item, index) in topAddrs">
            <span>第{{ index + 1 }}名 {{ item.addr }}</span>
            <b>{{ item.value }} ETH</b>
          </div>
        </div>

        <p class="tips-font ">排名说明：<br />
          1. 排名计算每局比赛（7天一个比赛周期）推广最多的10名用户。 <br />
          2. 当前排名为实时排名，随着比赛的进行，排名和奖励会发生变化。 <br />
          3. 每局比赛在开奖之后，可以在首页提取自己的奖金。 <br />
        </p>
      </el-tab-pane>

      <el-tab-pane label="昨日共振幸运名单">
        <div class="award-tips">幸运者分享奖金: <span class="gold-font balance-font">{{ luckyPool }}</span> ETH </div>

        <div class="addr-content">
          <div class="addr-font" v-bind:key="index" v-for="(item, index) in luckyAddrs">
            <span> {{ item.addr }}</span>
            <b>{{ item.value }} ETH</b>
          </div>
        </div>

        <p class="tips-font "> 幸运者说明：<br />
          1. 系统挑选每日最早参与游戏的10名用户作为幸运者。其中共振金额最高的用户分得1/4奖金。<br />
          2. 系统挑选每日最后参与游戏的10名用户作为幸运者。其中共振金额最高的用户分得1/4奖金。 <br />
          3. 除以上外，系统再挑选5名幸运者。<br />
        </p>

      </el-tab-pane>
    </el-tabs>

  </div>

</div>
</template>

<script>
/* eslint-disable */
import MyVid from "./../my_vid.js";
import formatNum from "./../numcomp";

export default {
  name: 'Ranking',
  data() {
    return {
      lastAwardEth: "~",
      round: 0,
      title: null,
      topAddrs: [],
      luckyAddrs: [],
      luckyPool: "~",
      loading: true
    }
  },

  created() {
    MyVid.initWeb3Account((web3, acc) => {
      this.account = acc
      this.web3 = web3
      this.init()
    });

  },
  methods: {

    goBack() {
      this.$router.back(-1)
    },

    async init() {
      this.pagoda = await MyVid.getPagoda()
      this.getRoundInfo()
      this.getLuckyAddrs()
    },

    poolInfo(round) {
      this.pagoda.top10Pool(round, {
        from: this.account
      }).then(r => {
        this.lastAwardEth = this.web3.utils.fromWei(r, 'ether')
        this.loading = false

        this.getTop10Addrs(round)
      })
    },

    getAward(index) {
      if (index == 0) {
        return this.lastAwardEth / 2
      } else if (index == 1) {
        return this.lastAwardEth / 5
      } else if (index == 2) {
        return this.lastAwardEth / 10
      } else if (index == 3) {
        return this.lastAwardEth * 6 / 100
      } else if (index == 4) {
        return this.lastAwardEth * 4 / 100
      } else {
        return this.lastAwardEth * (55 - 5 * index) / 1000;
      }
    },

    fn(num) {
      return formatNum(num, 4)
    },

    getTop10Addrs(round) {
      this.pagoda.getTop10(round, {
        from: this.account
      }).then(r => {
        for (var i = 0; i < r.length; i++) {
          let ev = this.fn(this.getAward(i))
          this.topAddrs.push({
            addr: r[i].substr(0, 8) + "..." + r[i].substr(37, 5),
            value: ev
          });
        }
        this.$forceUpdate();
      })
    },

    getLuckyAddrs() {
      let ts = Math.floor(new Date().getTime() / 1000);
      console.log(ts)

      this.pagoda.getDay(ts, {
        from: this.account
      }).then(r => {
        console.log("day " + r.toNumber())
        if (r.toNumber() >= 8711899) {
          this.pagoda.getDayAddrs(r.toNumber() - 1).then(r => {
            // luckyAddrs
            console.log(r[2])
            this.luckyPool = this.web3.utils.fromWei(r[2], 'ether')

            let luckypos = r[1]

            let first = luckypos[0].toNumber()
            let last = luckypos[1].toNumber()
            let ev = this.fn(this.luckyPool / 4)
            console.log(r[0])
            console.log(first + " , " + last)

            this.luckyAddrs.push({
              addr: r[0][first].substr(0, 8) + "..." + r[0][first].substr(37, 5),
              value: ev
            });
            this.luckyAddrs.push({
              addr: r[0][last].substr(0, 8) + "..." + r[0][first].substr(37, 5),
              value: ev
            });

            if (r[0].length > 25) {
              let ev1 = this.fn(this.luckyPool * 0.4 / 23)

              for (var i = 0; i < 10; i++) {
                if (i != first) {
                  this.luckyAddrs.push({
                    addr: r[0][i].substr(0, 8) + "..." + r[0][i].substr(37, 5),
                    value: ev1
                  });
                }
              }

              for (var i = luckypos[2].toNumber() + 10; i < 5; i++) {
                this.luckyAddrs.push({
                  addr: r[0][i].substr(0, 8) + "..." + r[0][i].substr(37, 5),
                  value: ev1
                });
              }

              for (var i = r[0].length - 10; i < r[0].length; i++) {
                if (i != last) {
                  this.luckyAddrs.push({
                    addr: r[0][i].substr(0, 8) + "..." + r[0][i].substr(37, 5),
                    value: ev1
                  });
                }
              }

            } else {
              let ev1 = this.luckyPool * 0.4 / (r[0].length - 2)

              for (var i = 0; i < r[0].length; i++) {
                if (i != first || i != last) {
                  this.luckyAddrs.push({
                    addr: r[0][i].substr(0, 8) + "..." + r[0][i].substr(37, 5),
                    value: ev1
                  });
                }
              }
            }
          })

        }

      })
    },

    getRoundInfo() {
      this.pagoda.getRound(Math.floor(new Date().getTime() / 1000)).then(
        r => {
          console.log(" round: " + r.toNumber());
          this.round = r.toNumber()
          this.poolInfo(this.round)
        })
    }
  }
}
</script>

