    <p class="s-font"> 距离第{{ round + 1 }}局开奖还有 {{ leftTime }} {{ timeUnit }}</p>
  </div>

  <div class="card-bkg ">
    <div  class="uhive-box" >
      <span> Top10 第{{ round }}期 </span>
      <el-button size="small" type="primary" plain=true @click="openTop10">开奖</el-button>
    </div>

    <div  class="uhive-box" >
      <span> 每日分发代币 </span>
      <el-button size="small" type="primary" plain=true @click="dispatch">分发</el-button>
    </div>

    <div class="uhive-box" >
      <span> 每日结算幸运者 </span>
      <el-button size="small" type="primary" plain=true @click="dayAward">开奖</el-button>
    </div>

  </div>

  <div>
    顶点推广链接: <el-link type="primary" :href="uLink"> {{ uLink }} </el-link>
  </div>

</div>
