<script>
/* eslint-disable */
// import formatNum from "./../numcomp";
import MyVid from "./../my_vid.js";

export default {
  name: 'Admin',
  data() {
    return {
      account: null,
      isOwner: false,
      leftTime: 10,
      round: 0,
      uLink: null,
      topestfund: 0,
      timeUnit: "天",
      needOpenTop10: false,
    }
  },
  created() {
    MyVid.initWeb3Account((web3, acc) => {
      this.account = acc
      this.web3 = web3
      this.init();
    });
  },
  methods: {

    async init() {
      this.pagoda = await MyVid.getPagoda()
      this.token = await MyVid.getVidToken()

      this.top = await MyVid.getTop()
      this.uhive = await MyVid.getuHive()

      this.info()
      this.getRoundInfo()
      this.checkOpenStatus();

    },

    getRoundInfo() {

      this.pagoda.getRound(Math.floor(new Date().getTime() / 1000)).then(
        r => {
          this.round = r.toNumber()
          console.log("round:" + this.round)
          this.pagoda.initTime({
            from: this.account
          }).then(
            r => {
              this.leftTime = MyVid.getLeftTime(r.toNumber())
              this.checkOpenStatus()
            }
          )
        })

        this.pagoda.getDay(Math.floor(new Date().getTime() / 1000),
        {from: this.account}
        ).then( r => {
          this.currDay = r.toNumber()
        })

    },

    dispatch() {
        this.pagoda.vidDispatch(this.currDay - 1 ,  {from: this.account} ).then(

        )
    },

    dayAward() {
      this.pagoda.openDay(this.currDay - 1 ,  {from: this.account} ).then(

        )
    },

    checkOpenStatus() {

    },

    info() {
      this.uLink = process.env.VUE_APP_HOST + 'p/' + this.top.address

      this.pagoda.etherRaised({
        from: this.account
      }).then(
        r => {
          this.ethRaised = this.web3.utils.fromWei(r, 'ether')
        }
      )

      this.pagoda.memberAward(this.top.address, {
        from: this.account
      }).then(
        r => {
          this.topestfund = this.web3.utils.fromWei(r, 'ether')
        }
      )

    },

    openTop10() {
      this.pagoda.open({
        from: this.account
      }).then(r => {})

    },

    topfund() {
      this.top.wdEth({
        from: this.account
      }).then(r => {

      })
    }

  }
}
</script>

